"use client";

import Link from "next/link";
import { useMemo, useState } from "react";
import { CheckCircle2 } from "lucide-react";

import { useCart } from "@/context/CartContext";
import { products } from "@/data/store";

function formatGBP(n: number) {
  return new Intl.NumberFormat("en-GB", { style: "currency", currency: "GBP" }).format(n);
}

function getPrice(p: any) {
  return Number(p?.base_price ?? p?.price ?? p?.unit_price ?? 0);
}

export default function CheckoutPage() {
  const { items, clearCart } = useCart() as any;

  const cartItems = Array.isArray(items) ? items : [];

  const rows = useMemo(() => {
    return cartItems
      .map((ci: any) => {
        const p = (products as any[]).find((x) => x.id === ci.productId);
        if (!p) return null;
        const price = getPrice(p);
        return { ci, p, price, lineTotal: price * (ci.qty ?? 1) };
      })
      .filter(Boolean) as any[];
  }, [cartItems]);

  const subtotal = useMemo(() => rows.reduce((sum, r) => sum + (r?.lineTotal ?? 0), 0), [rows]);
  const deliveryFee = subtotal > 0 ? 1.99 : 0;
  const total = subtotal + deliveryFee;

  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [note, setNote] = useState("");
  const [placing, setPlacing] = useState(false);
  const [done, setDone] = useState(false);

  async function placeOrder() {
    if (cartItems.length === 0) return;

    // very light validation (keep it simple)
    if (!name.trim() || !phone.trim() || !address.trim()) {
      alert("Please fill in Name, Phone, and Address.");
      return;
    }

    setPlacing(true);
    try {
      const productIds = cartItems.map((x: any) => x.productId);

      // ‚úÖ record bought-together
      await fetch("/api/copurchase/record", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ productIds }),
      });

      // (Optional) you can also POST an order somewhere later.
      // For now: demo checkout.
      clearCart?.();
      setDone(true);
    } finally {
      setPlacing(false);
    }
  }

  if (done) {
    return (
      <main className="min-h-screen bg-white text-black">
        <div className="max-w-xl mx-auto px-4 py-10">
          <div className="border rounded-2xl p-8 text-center">
            <CheckCircle2 className="w-12 h-12 mx-auto" />
            <h1 className="text-xl font-semibold mt-3">Order placed ‚úÖ</h1>
            <p className="text-sm text-gray-600 mt-2">
              Thanks! We also updated ‚Äúfrequently bought together‚Äù so recommendations get smarter.
            </p>

            <div className="mt-6 flex gap-3 justify-center">
              <Link href="/" className="px-5 py-2 rounded-full bg-blue-600 text-white text-sm font-semibold">
                Continue shopping
              </Link>
              <Link href="/cart" className="px-5 py-2 rounded-full border text-sm font-semibold">
                Back to cart
              </Link>
            </div>
          </div>
        </div>
      </main>
    );
  }

  return (
    <main className="min-h-screen bg-white text-black">
      <div className="max-w-5xl mx-auto px-4 py-6">
        <div className="flex items-center justify-between">
          <h1 className="text-lg font-semibold">Checkout</h1>
          <Link href="/cart" className="text-sm text-blue-600 hover:underline">
            ‚Üê Back to cart
          </Link>
        </div>

        <div className="mt-5 grid grid-cols-1 lg:grid-cols-[1.2fr_0.8fr] gap-6">
          {/* Form */}
          <section className="border rounded-2xl p-5">
            <h2 className="font-semibold">Delivery details</h2>

            <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-4">
              <label className="text-sm">
                <span className="text-gray-700">Full name</span>
                <input
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  className="mt-1 w-full border rounded-xl px-3 py-2 outline-none focus:ring-2 focus:ring-blue-200"
                  placeholder="Your name"
                />
              </label>

              <label className="text-sm">
                <span className="text-gray-700">Phone number</span>
                <input
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  className="mt-1 w-full border rounded-xl px-3 py-2 outline-none focus:ring-2 focus:ring-blue-200"
                  placeholder="e.g. 062..."
                />
              </label>

              <label className="text-sm md:col-span-2">
                <span className="text-gray-700">Address</span>
                <input
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                  className="mt-1 w-full border rounded-xl px-3 py-2 outline-none focus:ring-2 focus:ring-blue-200"
                  placeholder="Street, area, city"
                />
              </label>

              <label className="text-sm md:col-span-2">
                <span className="text-gray-700">Order note (optional)</span>
                <textarea
                  value={note}
                  onChange={(e) => setNote(e.target.value)}
                  className="mt-1 w-full border rounded-xl px-3 py-2 outline-none focus:ring-2 focus:ring-blue-200"
                  rows={3}
                  placeholder="Any instructions?"
                />
              </label>
            </div>
          </section>

          {/* Summary */}
          <aside className="border rounded-2xl p-5 h-fit">
            <h2 className="font-semibold">Order summary</h2>

            <div className="mt-4 space-y-3">
              {rows.map(({ ci, p, price, lineTotal }: any) => (
                <div key={`${ci.productId}`} className="flex justify-between text-sm">
                  <span className="text-gray-700">
                    {p.name} √ó {ci.qty ?? 1}
                  </span>
                  <span className="font-semibold">{formatGBP(lineTotal)}</span>
                </div>
              ))}
            </div>

            <div className="mt-4 border-t pt-4 space-y-2 text-sm">
              <div className="flex justify-between">
                <span className="text-gray-700">Subtotal</span>
                <span className="font-semibold">{formatGBP(subtotal)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-700">Delivery</span>
                <span className="font-semibold">{formatGBP(deliveryFee)}</span>
              </div>
              <div className="flex justify-between border-t pt-3">
                <span className="font-semibold">Total</span>
                <span className="font-semibold">{formatGBP(total)}</span>
              </div>
            </div>

            <button
              onClick={placeOrder}
              disabled={placing || cartItems.length === 0}
              className={`mt-5 w-full rounded-full py-3 text-sm font-semibold ${
                placing || cartItems.length === 0
                  ? "bg-gray-200 text-gray-500"
                  : "bg-blue-600 text-white hover:opacity-95"
              }`}
            >
              {placing ? "Placing order‚Ä¶" : "Place Order"}
            </button>

            <p className="text-xs text-gray-500 mt-3">
              This demo checkout records ‚Äúfrequently bought together‚Äù from your cart.
            </p>
          </aside>
        </div>
      </div>
    </main>
  );
}
